.feedback {
  // position: relative;
  width: 100%;

  @media (max-width: $mobiles-max) {
    padding: 0 16px;
  }
}

.feedback__inner {
  position: relative;
}

.feedback__form-wrap {
  @media (min-width: $desktops) {
    position: absolute;
    top: -526px;
    // transform: translateY(-626px);
    // bottom: 350px;
    left: 57.8%;
    width: 327px;
  }

  @media (max-width: $tablets-max) {
    width: 42.5%;
    margin: 0 auto;
  }

  @media (max-width: $mobiles-max) {
    width: 286.35px;
  }
}


.feedback__form-wrap p {
  text-align: center;
  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  margin: 23px 0 18px;
  color: #ffffff;

  @media (max-width: $tablets-max) {
    font-size: 18px;
    line-height: 28px;
    color: #000000;
    margin: 70px 0 28px;
  }

  @media (max-width: $mobiles-max) {
    margin: 71px 0 20px;
  }
}

.feedback__fieldset {
  display: flex;
  flex-direction: column;

  @media (max-width: $tablets-max) {
    margin-left: 2px;
  }

  @media (max-width: $mobiles-max) {
    margin-left: 0;
  }
}

.feedback__item {
  margin-bottom: 20px;

  // &--phone {
  //   margin-bottom: 17px;
  // }

  @media (max-width: $tablets-max) {
    margin-bottom: 18px;
    margin-right: 4px;
  }

  @media (max-width: $mobiles-max) {
    margin-right: 0;
  }
}

.feedback__fieldset input {
  width: 100%;
  border: 1px solid #c9ccd4;
  border-radius: 6px;
  // padding: 16px 0 14px 27px;
  padding: 13px 0 7px 27px;
  min-height: 48px;

  @media (max-width: $tablets-max) {
    padding: 17px 0 13px 20px;
  }

  @media (max-width: $mobiles-max) {
    padding: 18px 0 11px 24px;
  }

  &::placeholder {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.3px;

    @media (max-width: $tablets-max) {
      font-size: 15px;
      line-height: 24px;
      color: #1c1c1c;
      opacity: 0.5;
      letter-spacing: 0.25px;
    }
  }
}
